import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { specialRules } from '../databases/ArmyRules';
const iconStyle = { marginBottom: '0.2em' };

const ArmyCards = ({ currentList }) => {
  return (
    <>
      {currentList.map((e, index) => (
        <div className='card border-white mb-2'>
          <div className='card-header bg-transparent d-flex px-0'>
            <button className='btn text-left flex-grow-1 collapsed' data-toggle='collapse' data-target={'#num' + index + '-collapse'}>
              {e.name}{' '}
              <small className='text-muted'>
                {e.type === 'Cavalry' ? '/ Mounted' : ''}
                {e.type === 'Monster' ? '/ Flying' : ''}
                {e.type === 'Artillery' ? '/ Battery' : ''} / {e.points} pts
              </small>
            </button>
          </div>
          <div className='list-group list-group-flush collapse' id={'num' + index + '-collapse'}>
            <div className='list-group-item list-group-item-light d-flex'>
              <small className='flex-grow-1'>{e.size ? (e.size === 1 ? 'One stand ' + e.type + ' unit.' : 'Two stand ' + e.type + ' unit.') : 'Three stand ' + e.type + ' unit.'}</small>
            </div>
            <li className='list-group-item'>
              {e.attacks ? (
                <small className='pr-1'>
                  (<GiMailedFist style={iconStyle} /> {e.attacks})
                </small>
              ) : (
                <small className='pr-1'>
                  (<GiMailedFist style={iconStyle} /> –)
                </small>
              )}
              {e.shots ? (
                <small className='px-1'>
                  (<GiPocketBow style={iconStyle} /> {e.shots}{' '}
                  <small className='text-muted'>
                    {' '}
                    / {e.range}cm{e.omni ? ' 360°' : ''}
                  </small>
                  )
                </small>
              ) : (
                ''
              )}
              {e.hits ? (
                <small className='px-1'>
                  (<GiHeartPlus style={iconStyle} /> {e.hits})
                </small>
              ) : (
                <small className='px-1'>
                  (<GiHeartPlus style={iconStyle} /> –)
                </small>
              )}
              {e.armour ? (
                <small className='px-1'>
                  (<GiCheckedShield style={iconStyle} /> {e.armour})
                </small>
              ) : (
                <small className='px-1'>
                  (<GiCheckedShield style={iconStyle} /> –)
                </small>
              )}
              {e.command ? (
                <small className='px-1'>
                  (<GiFlyingFlag style={iconStyle} /> {e.command})
                </small>
              ) : (
                ''
              )}
            </li>
            {e.rules ? (
              e.rules.map((g) =>
                specialRules.map((f) =>
                  f.name === g ? (
                    <li className='list-group-item'>
                      <small className='card-text'>
                        <strong>{f.name}.</strong> {f.text}
                      </small>
                    </li>
                  ) : (
                    ''
                  )
                )
              )
            ) : (
              <li className='list-group-item'>
                <small className='card-text text-muted'>This unit has no special rules.</small>
              </li>
            )}
          </div>
        </div>
      ))}
    </>
  );
};

export default ArmyCards;
