import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { useState } from 'react';
import { specialRules } from '../databases/ArmyRules';
import keywordRules from '../databases/KeywordRules';
import { calculatePoints } from './Calculator-Points';
const iconStyle = { marginBottom: '0.2em' };

const Calculator = () => {
  // This is the calculator's state. The user's form submissions are saved as an object.
  const [calculatedUnit, setCalculatedUnit] = useState({
    name: 'Custom Name',
    type: 'Infantry',
    attacks: '–',
    shots: '–',
    hits: '–',
    armour: '–',
    command: '–',
    mount: '–',
    size: 3,
    points: '–',
    rules: ['–'],
  });

  // Whenever a form is updated, this event handler will recalculate the user's custom unit based on their selections.
  const handleChange = () => {
    // Name
    const nameValue = document.querySelector('#unitName').value;
    // Type
    const currentTypeIndex = document.querySelector('#unitType').selectedIndex;
    let currentTypeText = document.querySelector('#unitType').options[currentTypeIndex].innerHTML;
    const currentTypeValue = parseInt(document.querySelector('#unitType').value);
    // Attacks
    const attacksIndex = document.querySelector('#unitAttacks').selectedIndex;
    let attacksText = document.querySelector('#unitAttacks').options[attacksIndex].innerHTML;
    const attacksValue = parseInt(document.querySelector('#unitAttacks').value);
    // Shots
    const shotsIndex = document.querySelector('#unitShots').selectedIndex;
    let shotsText = document.querySelector('#unitShots').options[shotsIndex].innerHTML;
    const shotsValue = parseInt(document.querySelector('#unitShots').value);
    // Hits
    const hitsIndex = document.querySelector('#unitHits').selectedIndex;
    let hitsText = document.querySelector('#unitHits').options[hitsIndex].innerHTML;
    const hitsValue = parseInt(document.querySelector('#unitHits').value);
    // Armour
    const armourIndex = document.querySelector('#unitArmour').selectedIndex;
    let armourText = document.querySelector('#unitArmour').options[armourIndex].innerHTML;
    const armourValue = parseInt(document.querySelector('#unitArmour').value);
    // Command
    const commandIndex = document.querySelector('#unitCommand').selectedIndex;
    let commandText = document.querySelector('#unitCommand').options[commandIndex].innerHTML;
    const commandValue = parseInt(document.querySelector('#unitCommand').value);
    // Type
    const mountIndex = document.querySelector('#unitMount').selectedIndex;
    let mountText = document.querySelector('#unitMount').options[mountIndex].innerHTML;
    const mountValue = parseInt(document.querySelector('#unitMount').value);
    // Rules
    const rulesIndex = document.querySelector('#unitRules').selectedIndex;
    let rulesText = document.querySelector('#unitRules').options[rulesIndex].innerHTML;
    const rulesValue = parseInt(document.querySelector('#unitRules').value);
    // Size
    let sizeValue = 0;
    if (currentTypeText === 'Single-Artillery' || currentTypeText === 'Single-Monster' || currentTypeText === 'Machine' || currentTypeText === 'Single-Infantry') {
      sizeValue = 1;
    } else if (currentTypeText === 'Artillery') {
      sizeValue = 2;
    } else {
      sizeValue = 3;
    }
    // Points
    const pointsValue = calculatePoints(currentTypeValue, currentTypeText, attacksValue, shotsValue, hitsValue, armourValue, armourText, commandValue, mountValue, rulesValue);
    // Update State
    setCalculatedUnit((prev) => ({
      ...prev,
      name: nameValue ? nameValue : 'Custom Name',
      type: currentTypeText,
      attacks: attacksText,
      shots: shotsText,
      hits: hitsText,
      armour: armourText,
      command: commandText,
      mount: mountText,
      size: sizeValue,
      points: pointsValue,
      rules: [currentTypeText, attacksText, shotsText, hitsText, armourText, commandText, mountText, rulesText],
    }));
  };

  const handleReset = () => {
    document.querySelector('#unitAttacks').selectedIndex = 0;
    document.querySelector('#unitShots').selectedIndex = 0;
    document.querySelector('#unitHits').selectedIndex = 0;
    document.querySelector('#unitArmour').selectedIndex = 0;
    document.querySelector('#unitCommand').selectedIndex = 0;
    document.querySelector('#unitMount').selectedIndex = 0;
    document.querySelector('#unitRules').selectedIndex = 0;
    handleChange();
  };

  return (
    <>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Name</span>
        <input type='text' className='form-control' placeholder='Custom Name' id='unitName' onChange={() => handleChange()} />
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Type</span>
        <select className='form-select' id='unitType' onChange={() => handleReset()}>
          <option value={0}>–</option>
          <option value={0}>Infantry</option>
          <option value={0}>Single-Infantry</option>
          <option value={35}>Cavalry</option>
          <option value={35}>Chariot</option>
          <option value={0}>Machine</option>
          <option value={10}>Monster</option>
          <option value={10}>Single-Monster</option>
          <option value={0}>Artillery</option>
          <option value={0}>Single-Artillery</option>
          <option value={70}>General</option>
          <option value={70}>Hero</option>
          <option value={70}>Wizard</option>
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Attacks</span>
        <select className='form-select' id='unitAttacks' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? (
            <>
              <option value={10}>Self-defense Attacks</option> {/* 1 Attack */}
              <option value={20}>Light Attacks</option> {/* 2 Attack */}
              <option value={30}>Regular Attacks</option> {/* 3 Attack */}
              <option value={40}>Medium Attacks</option> {/* 4 Attack */}
              <option value={50}>Heavy Attacks</option> {/* 5 Attack */}
              <option value={60}>Magic Attacks</option> {/* 6 Attack */}
            </>
          ) : calculatedUnit.type === 'Single-Monster' || calculatedUnit.type === 'Machine' ? (
            <>
              <option value={5}>1</option>
              <option value={10}>2</option>
              <option value={15}>3</option>
              <option value={20}>4</option>
              <option value={25}>5</option>
              <option value={30}>6</option>
              <option value={35}>7</option>
              <option value={40}>8</option>
              <option value={45}>9</option>
              <option value={50}>10</option>
            </>
          ) : (
            <>
              <option value={5}>Self-defense Attacks</option> {/* 1 Attack */}
              <option value={20}>Light Attacks</option> {/* 2 Attack */}
              <option value={35}>Regular Attacks</option> {/* 3 Attack */}
              <option value={50}>Medium Attacks</option> {/* 4 Attack */}
              <option value={75}>Heavy Attacks</option> {/* 5 Attack */}
              <option value={125}>Magic Attacks</option> {/* 6 Attack */}
            </>
          )}
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Shots</span>
        <select className='form-select' id='unitShots' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? (
            <>
              <option value={20}>Magical Blast</option> {/* 2 / 20cm */}
            </>
          ) : calculatedUnit.type === 'Artillery' ? (
            <>
              <option value={50}>Battery of Springalds</option> {/* 3 / 40cm */}
              <option value={60}>Battery of Ballistae</option> {/* 2 per stand / 40cm */}
              <option value={80}>Battery of Cannons</option> {/* 2 +bounce / 60cm */}
            </>
          ) : calculatedUnit.type === 'Single-Monster' || calculatedUnit.type === 'Machine' ? (
            <>
              <option value={5}>Dragon Fire</option> {/* 2 / 20cm */}
              <option value={15}>Short-ranged Turret</option> {/* 3 / 15cm 360° */}
              <option value={25}>Long-ranged Turret</option> {/* 3 / 30cm 360° */}
            </>
          ) : calculatedUnit.type === 'Single-Artillery' ? (
            <>
              <option value={35}>Machine Gun</option> {/* 2 / 30cm */}
              <option value={40}>Flamethrower</option> {/* 2d6 / 30cm */}
              <option value={35}>Laser</option> {/* 1d6 / 40cm */}
              <option value={50}>Rockets</option> {/* 1d6 / 60cm */}
              <option value={65}>Mangonel</option> {/* 3 / 60cm */}
              <option value={75}>Trebuchet</option> {/* 4 / 80cm */}
            </>
          ) : (
            <>
              <option value={5}>Short-ranged Weapons</option> {/* 1 / 15cm */}
              <option value={15}>Long-ranged Weapons</option> {/* 1 / 30cm */}
              <option value={15}>Light Weapons</option> {/* 1 / 15cm 360° */}
              <option value={20}>Heavy Weapons</option> {/* 2 / 15cm */}
            </>
          )}
        </select>
      </div>
      <div className={calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? 'd-none input-group mb-3' : 'input-group mb-3'}>
        <span className='input-group-text col-3 bg-light'>Hits</span>
        <select className='form-select' id='unitHits' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'Single-Monster' || calculatedUnit.type === 'Machine' ? (
            <>
              <option value={-20}>Small</option> {/* 2 Hits */}
              <option value={-5}>Large</option> {/* 4 Hits */}
              <option value={0}>Huge</option> {/* 6 Hits */}
              <option value={5}>Massive</option> {/* 8 Hits */}
              <option value={20}>Towering</option> {/* 10 Hits */}
            </>
          ) : (
            <>
              <option value={0}>Poor Health</option> {/* 2 Hits */}
              <option value={5}>Good Health</option> {/* 3 Hits */}
              <option value={20}>Exceptional Health</option> {/* 4 Hits */}
            </>
          )}
        </select>
      </div>
      <div className={calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? 'd-none input-group mb-3' : 'input-group mb-3'}>
        <span className='input-group-text col-3 bg-light'>Armour</span>
        <select className='form-select' id='unitArmour' onChange={() => handleChange()}>
          <option value={0}>–</option> {/* 0 Armour */}
          <option value={5}>Light Armour</option> {/* 6+ Armour */}
          <option value={20}>Medium Armour</option> {/* 5+ Armour */}
          <option value={35}>Heavy Armour</option> {/* 4+ Armour */}
          <option value={50}>Magic Armour</option> {/* 3+ Armour */}
        </select>
      </div>
      <div className={calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? 'input-group mb-3' : 'd-none input-group mb-3'}>
        <span className='input-group-text col-3 bg-light'>Command</span>
        <select className='form-select' id='unitCommand' onChange={() => handleChange()}>
          <option value={0}>–</option>
          <option value={-40}>Poor Command</option> {/* 6 Command */}
          <option value={-35}>Good Command</option> {/* 7 Command */}
          <option value={0}>Great Command</option> {/* 8 Command */}
          <option value={35}>Exceptional Command</option> {/* 9 Command */}
          <option value={90}>Magic Command</option> {/* 10 Command */}
        </select>
      </div>
      <div className={calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? 'input-group mb-3' : 'd-none input-group mb-3'}>
        <span className='input-group-text col-3 bg-light'>Mount</span>
        <select className='form-select' id='unitMount' onChange={() => handleChange()}>
          <option value={0}>–</option>
          <option value={0}>Standard Mount</option>
          <option value={5}>Magical Mount</option> {/* Innate Spellcasting */}
          <option value={10}>Inspiring Mount</option> {/* Innate Command */}
          <option value={10}>Warded Mount</option> {/* Innate Dispelling */}
          <option value={10}>Flying Mount</option> {/* Aerial Commander */}
          <option value={30}>Terrifying Mount</option> {/* Terrifying Presence */}
          <option value={40}>Winged Terror Mount</option> {/* Terrifying Presence & Aerial Commander */}
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Rules</span>
        <select className='form-select' id='unitRules' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? (
            <>
              <option value={5}>Inspiring Leader</option>
              <option value={5}>Inspiring Aura</option>
              <option value={10}>Arcane Mastery</option>
              <option value={25}>Spellcaster</option>
              <option value={40}>Spell Channeling</option>
              <option value={-25}>Cruel Leader</option>
              <option value={0}>Uncharismatic Leader</option>
              <option value={0}>Eldritch Leader</option>
            </>
          ) : (
            <>
              <option value={0}>Undead</option>
              <option value={0}>Eldritch</option>
              <option value={0}>Daemonic</option>
              <option value={0}>Verminous</option>
              <option value={0}>Knight</option>
              <option value={0}>Bushido</option>
              <option value={0}>Mutant</option>
              <option value={15}>Outrider</option>
              <option value={20}>Regenerates</option>
              <option value={20}>Pursuer</option>
              <option value={20}>Mobile Fortress</option>
              <option value={25}>Ambusher</option>
              <option value={30}>Flying</option>
              <option value={30}>Gigantic</option>
              <option value={20}>Elephant</option>
              <option value={40}>Terrifying</option>
              <option value={10}>Immune to Terror</option>
              <option value={10}>Precise Shots</option>
              <option value={10}>Armour Piercing Shots</option>
              <option value={10}>Explosive Shots</option>
              <option value={15}>Quick Reload</option>
              <option value={15}>Devastating Charge</option>
              <option value={20}>Aggressive</option>
              <option value={15}>Devastating Shots</option>
              <option value={-5}>Hungry for Humans</option>
              <option value={-10}>Disorderly</option>
              <option value={-10}>Fanatic</option>
              <option value={-10}>Short Supply</option>
              <option value={-20}>Bloodlust</option>
              <option value={-10}>Feeble</option>
            </>
          )}
        </select>
      </div>
      <h3 className='text-center lead mt-5'>Your Custom {calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? 'Character' : 'Unit'}</h3>
      <div className='card border-white mb-2'>
        <div className='card-header bg-transparent d-flex px-0'>
          <div className='px-3 text-left flex-grow-1'>
            {calculatedUnit.name} <small className='text-muted'>/ {calculatedUnit.points} pts</small>
          </div>
        </div>
        <div className='list-group list-group-flush'>
          <div className='list-group-item list-group-item-light d-flex'>
            <small className='flex-grow-1'>{calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? calculatedUnit.type + ' character.' : calculatedUnit.size === 1 ? 'One stand ' + calculatedUnit.type + ' unit.' : calculatedUnit.size === 2 ? 'Two stand ' + calculatedUnit.type + ' unit.' : calculatedUnit.size === 3 ? 'Three stand ' + calculatedUnit.type + ' unit.' : ''}</small>
            <small>Warmaster Pocket</small>
          </div>

          {/* Statline as calculated by the form choices */}
          <li className='list-group-item'>
            <small className='pr-1'>
              (<GiMailedFist style={iconStyle} /> {calculatedUnit.attacks === 'Self-defense Attacks' ? '1' : calculatedUnit.attacks === 'Light Attacks' ? '2' : calculatedUnit.attacks === 'Regular Attacks' ? '3' : calculatedUnit.attacks === 'Medium Attacks' ? '4' : calculatedUnit.attacks === 'Heavy Attacks' ? '5' : calculatedUnit.attacks === 'Magic Attacks' ? '6' : '–'})
            </small>
            {calculatedUnit.shots === '–' ? (
              ''
            ) : (
              <small className='px-1'>
                (<GiPocketBow style={iconStyle} />{' '}
                {calculatedUnit.shots === 'Magical Blast' ? (
                  <>
                    2 <small className='text-muted'> / 20cm</small>
                  </>
                ) : calculatedUnit.shots === 'Battery of Springalds' ? (
                  <>
                    3 <small className='text-muted'> / 40cm</small>
                  </>
                ) : calculatedUnit.shots === 'Battery of Ballistae' ? (
                  <>
                    2 <small className='text-muted'> / 40cm</small>
                  </>
                ) : calculatedUnit.shots === 'Battery of Cannons' ? (
                  <>
                    2 <small className='text-muted'> / 60cm</small>
                  </>
                ) : calculatedUnit.shots === 'Dragon Fire' ? (
                  <>
                    2 <small className='text-muted'> / 20cm</small>
                  </>
                ) : calculatedUnit.shots === 'Short-ranged Turret' ? (
                  <>
                    3 <small className='text-muted'> / 15cm 360°</small>
                  </>
                ) : calculatedUnit.shots === 'Long-ranged Turret' ? (
                  <>
                    3 <small className='text-muted'> / 30cm 360°</small>
                  </>
                ) : calculatedUnit.shots === 'Machine Gun' ? (
                  <>
                    2 <small className='text-muted'> / 30cm</small>
                  </>
                ) : calculatedUnit.shots === 'Flamethrower' ? (
                  <>
                    2d6 <small className='text-muted'> / 30cm</small>
                  </>
                ) : calculatedUnit.shots === 'Laser' ? (
                  <>
                    1d6 <small className='text-muted'> / 40cm</small>
                  </>
                ) : calculatedUnit.shots === 'Rockets' ? (
                  <>
                    1d6 <small className='text-muted'> / 60cm</small>
                  </>
                ) : calculatedUnit.shots === 'Mangonel' ? (
                  <>
                    3 <small className='text-muted'> / 60cm</small>
                  </>
                ) : calculatedUnit.shots === 'Trebuchet' ? (
                  <>
                    4 <small className='text-muted'> / 80cm</small>
                  </>
                ) : calculatedUnit.shots === 'Short-ranged Weapons' ? (
                  <>
                    1 <small className='text-muted'> / 15cm</small>
                  </>
                ) : calculatedUnit.shots === 'Long-ranged Weapons' ? (
                  <>
                    1 <small className='text-muted'> / 30cm</small>
                  </>
                ) : calculatedUnit.shots === 'Light Weapons' ? (
                  <>
                    1 <small className='text-muted'> / 15cm 360°</small>
                  </>
                ) : calculatedUnit.shots === 'Heavy Weapons' ? (
                  <>
                    2 <small className='text-muted'> / 15cm</small>
                  </>
                ) : (
                  '–'
                )}
                )
              </small>
            )}
            {calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? (
              <small className='px-1'>
                (<GiFlyingFlag style={iconStyle} /> {calculatedUnit.command === 'Poor Command' ? '6' : calculatedUnit.command === 'Good Command' ? '7' : calculatedUnit.command === 'Great Command' ? '8' : calculatedUnit.command === 'Exceptional Command' ? '9' : calculatedUnit.command === 'Magic Command' ? '10' : '–'} <small className='text-muted'>{calculatedUnit.type === 'General' ? ' / ∞ cm' : calculatedUnit.type === 'Hero' ? ' / 60cm' : ' / 20cm'}</small>)
              </small>
            ) : (
              <>
                <small className='px-1'>
                  (<GiHeartPlus style={iconStyle} /> {calculatedUnit.hits === 'Poor Health' ? '2' : calculatedUnit.hits === 'Good Health' ? '3' : calculatedUnit.hits === 'Exceptional Health' ? '4' : calculatedUnit.hits === 'Small' ? '2' : calculatedUnit.hits === 'Large' ? '4' : calculatedUnit.hits === 'Huge' ? '6' : calculatedUnit.hits === 'Massive' ? '8' : calculatedUnit.hits === 'Towering' ? '10' : '–'})
                </small>
                <small className='px-1'>
                  (<GiCheckedShield style={iconStyle} /> {calculatedUnit.armour === 'Light Armour' ? '6+' : calculatedUnit.armour === 'Medium Armour' ? '5+' : calculatedUnit.armour === 'Heavy Armour' ? '4+' : calculatedUnit.armour === 'Magic Armour' ? '3+' : '–'})
                </small>
              </>
            )}
          </li>
          {calculatedUnit.hits > 5 ? (
            <li className='list-group-item'>
              <small className='card-text'>
                {specialRules.map((e) =>
                  e.name === 'Wounded State' ? (
                    <>
                      <strong>{e.name}.</strong> {e.text}
                    </>
                  ) : (
                    ''
                  )
                )}
              </small>
            </li>
          ) : (
            ''
          )}
          {/*
            <li className='list-group-item'>
              <small className='card-text text-muted'>This {calculatedUnit.type === 'General' || calculatedUnit.type === 'Hero' || calculatedUnit.type === 'Wizard' ? 'character' : 'unit'} has no special rules.</small>
            </li>
          */}
          {/* Optional special rules chosen by the user */}
          {calculatedUnit.rules.map((e) =>
            keywordRules.map((f) =>
              e === f.name
                ? f.rules.map((g) =>
                    specialRules.map((h) =>
                      h.name === g ? (
                        <li className='list-group-item'>
                          <small className='card-text'>
                            <strong>{h.name}.</strong> {h.text}
                          </small>
                        </li>
                      ) : (
                        ''
                      )
                    )
                  )
                : ''
            )
          )}
        </div>
      </div>
    </>
  );
};

export default Calculator;
