export const calculatePoints = (currentTypeValue, currentTypeText, attacksValue, shotsValue, hitsValue, armourValue, armourText, commandValue, mountValue, rulesValue) => {
  // Base Cost
  let totalPoints = 0;
  totalPoints += currentTypeValue + attacksValue + shotsValue + hitsValue + armourValue + commandValue + mountValue + rulesValue;
  // Taxes
  // -- TAXES START --
  // Red Taxes
  let redTax = 0;
  if (currentTypeText !== 'Single-Monster' && currentTypeText !== 'Machine') {
    redTax += attacksValue > 35 ? 1 : 0;
    redTax += hitsValue > 5 ? 1 : 0;
    redTax += armourValue > 5 ? 1 : 0;
  } else {
    redTax += attacksValue > 5 ? 1 : 0;
    redTax += hitsValue > 0 ? 1 : 0;
    redTax += armourValue > 5 ? 1 : 0;
  }
  if (redTax === 2) {
    totalPoints += 25;
  }
  if (redTax === 3) {
    totalPoints += 45;
  }
  // Clifford Taxes
  let cliffordTax = 0;
  if (currentTypeText !== 'Single-Monster' && currentTypeText !== 'Machine') {
    cliffordTax += attacksValue > 50 ? 1 : 0;
    cliffordTax += hitsValue > 5 ? 1 : 0;
    cliffordTax += armourValue > 20 ? 1 : 0;
  } else {
    cliffordTax += attacksValue > 10 ? 1 : 0;
    cliffordTax += hitsValue > 5 ? 1 : 0;
    cliffordTax += armourValue > 35 ? 1 : 0;
  }
  if (cliffordTax > 1) {
    totalPoints += 20;
  }
  // Tax Rebates
  if (currentTypeText === 'Single-Infantry' && totalPoints > 30) {
    totalPoints -= 30;
  }
  if (currentTypeText === 'Cavalry' && (armourText === '–' || armourText === '6+' || armourText === '5+')) {
    totalPoints -= 15;
  }
  if (redTax > 1 && currentTypeText === 'Infantry') {
    totalPoints -= 25;
  }
  if (cliffordTax > 1 && (currentTypeText === 'Single-Monster' || currentTypeText === 'Machine')) {
    totalPoints -= 20;
  }
  // -- TAXES END --
  return totalPoints;
};
